.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

div{text-align: left;}
h4, p{margin:0;}
.black-nav{padding:20px; background:#000;}
.black-nav h4{color:#fff;}
.list{padding:20px; border-bottom:1px solid #000;}
.list h4{margin-bottom:10px;}
.list button{margin-top:10px;}
.modal{position:relative; margin-top:20px; padding:20px; background:#eee;}
.modal > *{margin-bottom:15px;}
.modal > *:last-child{margin-bottom:0px;}
.modal .close{position:absolute; top:10px; right:10px;}
.input-box{display:flex; padding:20px;}
.input-box button{margin-left:10px;}